<template>
    <div id="main">
        <nav><a>Waiting</a><span>&nbsp;>&nbsp;</span><a></a></nav>
        <div class="input">
            <p class="pet_class"><span>:</span><select v-model="pet_class"><option></option><option></option><option></option></select><i></i>
            <span>:</span><select v-model="pet_city"><option></option><option></option><option></option></select><i></i>
            </p>
            <p class="pet_name"><span>:</span><input type="text" maxlength="10" v-model="pet_name" ><i></i></p>
            <p class="pet_title"><span>:</span><input type="text" maxlength="30" v-model="pet_title" ><i></i></p>
            <p class="pet_detail"><span> (  ) : </span></p>
            <p class="pet_text"><textarea v-model="pet_text"></textarea><!-- 800*250 --></p>
            <div class="file">
              <h4> (  ) :</h4>
              <input type="file"  id="file" @change="file()"></input><i></i>
              <input type="file" disabled></input>
              <input type="file" disabled></input>
              <input type="file" disabled></input>
            </div>
            <p > :  <i>  </i> </p>
        </div>
        <button class="SubPet" @click="subPet"></button>
    </div>
</template>
<script>
    export default{
        data() {
          return {
            pet_class:"",
            pet_city:"",
            pet_name:"",
            pet_title:"",
            pet_text:"",
            pet_file:"",
            uid:"",
            img1:"",
            ser_id:""
          }
        },
        created() {
          //
          if(!this.$store.state.islogin){
            alert("!");
            this.$router.push({path: '/login'})
          }
        },
        watch: {
          pet_class(){
            console.log(this.pet_class);
          }
        },
        methods: {
          file(){
            this.pet_file=1;
          },
          subPet(){
            var uname = this.$store.state.uname;
            var ser_id="";
            //console.log(uname);
            if(this.pet_file>0 && this.pet_name.length>0 && this.pet_title.length>0){
            //promise  ----------------------------------
              this.axios.post('http://127.0.0.1:3000/user/reg/sea_uname',Qs.stringify({uname})).then(res=>{
                this.uid = res.data[0].uid;
                    //promise  ----------------------------------
                    var pet_class=this.pet_class
                    var formData = new FormData() // FormData
                    var formData = new window.FormData()
                    formData.append('file', document.querySelector('#file').files[0])
                    formData.append('pet_class',pet_class)
                    var options = {  // axios
                    url: 'http://127.0.0.1:3000/pet/upload',
                    data: formData,
                    method: 'post',
                    headers: { 
                      'Content-Type': 'multipart/form-data'
                    }
                    }
                    this.axios(options).then((res) => {
                      this.img1 = res.data.img1;
                      this.ser_id = res.data.ser_id;
                      
                          //!promise  ----------------------------------
                          this.axios.post('http://127.0.0.1:3000/pet/addpet',Qs.stringify({pet_class:this.pet_class,pet_city:this.pet_city,pet_name:this.pet_name,pet_title:this.pet_title,pet_text:this.pet_text,uid:this.uid,img1:this.img1,ser_id:this.ser_id,pet_class:this.pet_class,
                          master:this.$store.state.uname})).then(res=>{
                            if(res.data.success==200){
                              alert(",!!")
                              this.$router.push({path: '/'})
                              location.reload();
                            }else{
                              alert("Sorry!,!")
                            }
                          })
                     }) 
            })
            }else{
              alert('!');
            }




















          }
        },
    }
</script>
<style scoped>
    #main{
    margin: 0 auto;
    margin-top:70px; 
    margin-bottom:10px; 
    width: 1200px;
    height: 850px;
    border: 1px solid transparent;
    box-shadow: 0 0 4px rgba(250,136,83,0.8)
  }
    #main>nav{
    width: 90%;
    height: 40px;
    text-align: left;
    line-height: 40px;
    margin:10px 60px;
    margin-bottom:0px;  
    border-bottom:1px solid #e6e6e6;
  }
  #main>nav a:hover{
    text-decoration: underline;
    cursor: pointer;
  }
  /* input */
  div.input{
    width: 90%;
    height: 650px;
    margin: 0 auto;
    margin-top:20px; 
  }
  div.input i{
    color: red;
  }
  div.input p{
    margin-left: 100px;
    text-align: left;
  }
  div.input p span{
      display: inline-block;
      height: 30px;
      width: 100px;
      font-size: 18px;
      color:#666 ;
      text-align: right;
      padding-right: 20px;
  }
  .pet_class select{
    width: 80px;
    height: 25px;
    border-radius: 4px;
  }
  .pet_name input,.pet_title input{
    width: 160px;
    height: 25px;
    border-radius: 4px;
    border: 1px solid #aaa;
  }
  .pet_title input{
    width:400px;
  }
  div.input p{
    margin-top: 20px;
  }
  .pet_detail span{
    width: 200px !important;
  }
  .pet_text{
    margin-top: 0 !important;
  }
  .pet_text textarea{
    margin-left: 50px;
    width: 776px;
    height: 280px;
    font-size: 22px;
    resize:none;
    border-radius:5px; 
  }
  div.file h4{
    text-align: left;
    padding-left: 110px;
  }
  div.file input{
    width: 170px;
    margin: 10px 0 0 20px;
  }
  .SubPet{
    width: 200px;
    height: 40px;
  }
</style>